<template>
  <div class="container">
    <h1>Senaste Omd√∂men</h1>
    <!--Create post here-->
    <div class="create-post">
      <h2>Skriv Omd√∂me</h2>
      <input id="search" v-model="search" placeholder="S√∂k Elev">
      <form id="form-wrapper"> 
      <input  type="text" name="create-name" id="create-name" v-model="namn" placeholder="Namn" required><br><br>
      <input type="text" name="create-moment" id="create-moment" v-model="moment" placeholder="Moment" required><br><br>
      <select name="omdome" placeholder="Omd√∂me" id="omdome" v-model="omdome" required>
        <option value="" disabled selected hidden>Omd√∂me</option>
        <option value="A" selected>A</option>
        <option value="B">B</option>
        <option value="C">C</option>
        <option value="D">D</option>
        <option value="E">E</option>
        <option value="F">F</option>
      </select><br><br>

      <input type="submit" value="Spara omd√∂me" v-on:click="createPost">
      </form>
    </div>
    <hr>
    <p class="error" v-if="error">{{error}}</p>
    <div class="posts-container">
      <div class="post"
      v-for="(post, index) in filteredList"
      v-bind:item="post"
      v-bind:index="index"
      v-bind:key="post._id"
      v-on:dblclick="deletePost(post._id)"
      >
      <div v-if="editPost == post._id">
                <input v-on:keyup.13="updatePost(post.namn,post.moment,post.omdome,post._id)" v-model="post.namn" />
                <input v-on:keyup.13="updatePost(post.namn,post.moment,post.omdome,post._id)" v-model="post.moment"/>
                <select v-on:keyup.13="updatePost(post.namn,post.moment,post.omdome,post._id)" v-model="post.omdome">
                <option value="A">A</option>
                <option value="B">B</option>
                <option value="C">C</option>
                <option value="D">D</option>
                <option value="E">E</option>
                <option value="F">F</option>
                </select>
                <button type="submit" v-on:click="updatePost(post.namn,post.moment,post.omdome,post._id)">Spara</button>
          </div>
          <div v-else>

      {{`${post.createdAt.getDate()}/${post.createdAt.getMonth()}/${post.createdAt.getFullYear()}`}}<br>
      <div class="whole-container">
      <div class="text-container">
        
      <p class="text">Namn<br><br>{{post.namn}}</p>
      <p class="text">Moment<br><br>{{post.moment}}</p>
      <p class="text">Omd√∂me<br><br>{{post.omdome}}</p>
      
      </div>
      <button class="edit" v-on:click="editPost = post._id">&#x270E;</button>
      <button class="delete" v-on:click="deletePost(post._id)">&#x1F5D1;</button>
      
      
      </div>
      
      </div>
    </div>
    </div>
<!--Ny tart-->
    
<!--Ny Slut-->
  </div>
  
</template>

<script>
import PostService from '../PostService';

export default {
  name: 'PostComponent',
  data(){
    return {
      posts: [],
      error: '',
      namn: '',
      moment: '',
      omdome: '',
      editPost: null,
      search: '',
    }
  },
  async created(){
    try{
      this.posts = await PostService.getPosts();
    }
    catch (err){
      this.error = err.message;
    }
  },
  methods: {
    async createPost(){
      await PostService.insertPost(this.namn,this.moment,this.omdome);
      this.posts = await PostService.getPosts();
    },
     async deletePost(id){
      await PostService.deletePost(id);
      this.posts = await PostService.getPosts();
    },
    async updatePost(namn,moment,omdome,id){
      await PostService.updatePost(namn,moment,omdome,id);
      this.editPost = null;
      this.posts = await PostService.getPosts();
      
    },
    test(namn){
        console.log(JSON.stringify(namn));
    },
  },
  computed: {
    filteredList() {
		return this.posts.filter(post => {
       var self = this;
       return post.namn.toLowerCase().includes(self.search.toLowerCase())
     })
	}
	}
};
</script>

